@import url("https://fonts.googleapis.com/css2?family=Bree+Serif&family=Monda:wght@700&family=Poppins:wght@600&display=swap");
html {
  font-size: 70%;
  box-sizing: border-box;
}
:root {
  --primaryColor: rgb(5, 170, 27);
  --secondaryColor: aliceblue;
  --darkgreen: rgb(3, 107, 17);
  --mainTransition: all 3s ease-out;
}
body {
  margin: 0;
  padding: 0;
  position: relative;
  background-image: url("./Statics/bgImg.jpg");
  background-size: cover;
  background-attachment: fixed;
  font-size: 1.6rem;
  font-family: "Bree Serif", serif;
  z-index: 0;
}

body::after {
  background-image: url("./Statics/bubs-1.gif");
  content: "";
  opacity: 0.8;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: -1;
}
button {
  font-size: 1.6rem;
  font-family: "Monda", sans-serif;
}
h2 {
  font-family: "Monda", sans-serif;
}

a {
  text-decoration: none;
}
/* li {
  
  list-style-type: none;
} */
/* header-section */
header {
  grid-area: header;
  color: aliceblue;
  height: 5rem;
  padding: 1rem;
  z-index: 1;
}
.nav-center {
  display: flex;
  justify-content: space-between;
}
.nav-center div {
  padding: 1rem;
}
.nav-middle {
  display: flex;
  padding: 1rem;
}
.menu ul {
  display: flex;
  text-align: center;
  letter-spacing: 1px;
  font-size: 2rem;
  /* background-color: rgba(200, 41, 41, 0.048); */
}

.menu a {
  color: aliceblue;
  padding: 1rem;
}
.menu a:hover {
  color: var(--primaryColor);
}
.menu li:hover {
  color: var(--primaryColor);
}
.title {
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
}
.title h1 {
  font-size: 3rem;
  margin-left: 1rem;
  color: var(--primaryColor);
}
.header-icon {
  font-size: 2.5rem;
}
.header-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;

  color: var(--primaryColor);
}
.header-links {
  height: 0;
  overflow: hidden;
  list-style-type: none;
  transition: 0.5s ease-out;
}
.show-header {
  height: auto;
  transition: height 3s;
  transition: 1s ease-in;
}
/* End header-section */

/* main-section */
main {
  grid-area: main;
  margin-top: 8rem;
}
.intro {
  color: aliceblue;
  align-items: center;
  text-align: center;
  font-size: 2rem;
}
.info {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-image {
  width: 30rem;
  height: 30rem;
  background-image: url("./Statics/image2.png");
  background-size: cover;
  margin-left: 5%;
  border-radius: 50%;
  /* filter: grayscale(30%); */
  opacity: 0;
  transform: translateY(-500px);
}
button {
  border: none;
  background-color: var(--darkgreen);
  color: aliceblue;
  font-size: 1.5rem;
  height: 3rem;
  border-radius: 5px;
}

.body-btn:hover {
  border: 1.5px solid white;
  transition: ease-in;
}
.arrow-down {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  color: var(--darkgreen);
  margin-top: 1px;
  font-size: 1.5rem;
  animation-name: arrows;
  animation-duration: 2s;
  animation-delay: 2s;
  animation-iteration-count: 2;
}
@keyframes arrows {
  0% {
    padding-top: 0%;
  }
  25% {
    padding-top: 5px;
  }
  50% {
    padding-top: 10px;
  }
  75% {
    padding-top: 15px;
  }
  100% {
    padding-top: 20px;
  }
}
.welcome p {
  color: rgb(209, 208, 208);
  font-family: "Bree Serif", sans-serif;
  font-weight: 500;
}
.detail {
  padding: 3rem;
}

.detail h2 {
  font-weight: 600;
  padding: 0.5rem;
  text-align: center;
  color: aliceblue;
  text-decoration: underline;
  text-decoration-color: #fff;
  /* text-decoration-thickness: 0.5rem; */
}

.about-section {
  padding: 5rem;
  width: 50%;
  position: relative;
}
.about-section div {
  display: flex;
  align-items: left center;
  justify-content: center;
  padding-top: 1rem;
}
.about-section div button {
  width: 15rem;
}
.about-section p {
  font-family: "Poppins", sans-serif;
  font-size: larger;
  font-weight: 900;
  color: rgb(209, 208, 208);
}

.space {
  padding: 2rem;
}
.works-section {
  /* background-color: rgb(175, 184, 199); */
  padding: 3rem;
  width: auto;
  /* color: aliceblue; */

  /* align-items: center; */
}
.works-section h2 {
  color: aliceblue;
  padding: 0.5rem;
}

.works-section h3 {
  padding: 0.5rem;
  color: rgb(209, 208, 208);
  text-align: center;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.cards div {
  display: flex;
  flex-direction: column;
  width: 37rem;
  justify-content: space-between;
  height: 37rem;
  margin: 3rem;
  padding: 1rem;
  transition: transform 0.2s;
  border-left: 5px solid var(--darkgreen);
  border-right: 5px solid var(--darkgreen);
  border-radius: 20px;
}
.cards a {
  padding: 0.5rem;

  text-align: center;
  /* width: 30%; */
}

.cards div:hover {
  width: 35.5rem;
}
.works-section a:hover {
  text-decoration: underline;
  text-decoration-color: lightgreen;
  cursor: pointer;
}

/* End main-section */
form {
  grid-area: main;
  width: 40rem;
  height: auto;
  display: flex;
  padding: 5rem;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  align-content: center;
}
form > h1 {
  margin: 20px;
  color: var(--primaryColor);
  text-align: center;
}
form > input,
textarea {
  padding: 20px;
  border-radius: 3px;
  margin-bottom: 20px;
  border: 1px solid lightgray;
  background: rgb(180, 180, 180);
  font-size: 16px;
  color: rgb(0, 0, 10);
  outline: none;
}
form > input:focus,
textarea:focus {
  border: 1px solid var(--primaryColor);
}
form > textarea {
  height: 145px;
  max-width: 400px;
  min-height: 100px;
}
form > label {
  padding-bottom: 10px;
  color: rgb(255, 255, 255);
  font-weight: bold;
}
form > button {
  padding: 20px;
  border: none;
  height: 4.5rem;
  background-color: var(--primaryColor);
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}
form > button:hover {
  border: 1px solid white;
}
.Social ul {
  align-items: center;
  display: flex;
  justify-content: center;
}
.Social li {
  display: flex;
  justify-content: space-evenly;
  padding: 2rem;
  font-size: 3.5rem;
  color: white;
}
/* End contact */
/* footer-section */
footer {
  /* grid-area: footer; */
  color: rgba(192, 198, 209, 0.548);
  display: flex;
  font-size: 1.3rem;
  font-style: oblique;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  background-color: rgba(9, 9, 29, 0.842);
}
/* End footer-section */
.skills {
  margin: 5rem 5rem 5rem 5rem;
  color: rgb(160, 160, 160);
  font-size: 2rem;
  font-weight: 400;
  list-style-type: circle;
}
.skills h2 {
  text-decoration: underline;
  color: #fff;
}
.getin-touch {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
  padding: 3rem;
}
.getin-touch button {
  height: 4rem;
  width: 15rem;
}
.getin-touch a {
  text-decoration: none;
  color: aliceblue;
}
@media screen and (max-width: 600px) {
  header {
    height: 10rem;
    padding: 0.5rem;
  }
  .arrow-down {
    display: none;
  }
  .info {
    display: block;
    padding: 1rem;
    text-align: center;
  }
  .show-header {
    height: 10rem;
  }
  .profile-image {
    margin-left: 10%;
    font-size: medium;
  }
  .line {
    margin-left: 7rem;
  }
  .detail button {
    font-size: 1.5rem;
    margin-left: 35%;
  }
  .works-section a {
    font-size: 1.2rem;
  }
  .about-section {
    width: auto;
    font-size: 1.3rem;
    font-weight: 500;
  }
  .skills {
    margin: 5rem 0 5rem 0.5rem;
    /* color: aliceblue; */
    font-size: 2rem;
    font-weight: 500;
  }
  .menu ul {
    display: block;
    text-align: center;
    letter-spacing: 1px;
    font-size: 2rem;
    /* margin-top: -0.2rem; */
  }
}
@media screen and (max-width: 768px) {
  header {
    height: 5rem;
    padding: 0.5rem;
  }
  .info {
    display: block;
    padding: 2rem;
    text-align: center;
  }
  .profile-image {
    margin-left: 10%;
  }
  .line {
    margin-left: 13rem;
  }
  .works-section a {
    font-size: 1.5rem;
    padding: 0.5rem;
  }
  form {
    width: 37rem;
  }
  .arrow-down {
    display: none;
  }
  .skills {
    margin: 5rem 0 5rem 0.5rem;
    /* color: aliceblue; */
    font-size: 2rem;
    font-weight: 500;
  }
  .show-header {
    height: 10rem;
  }
}
@media screen and (max-width: 992px) {
  .info {
    display: block;
    padding: 2rem;
    text-align: center;
  }
  .profile-image {
    margin-left: 10%;
  }
  .line {
    margin-left: 20rem;
  }
  .show-header {
    height: 10rem;
  }
}
@media screen and (min-width: 1024px) {
  body {
    padding: 0 5rem 0 5rem;
  }
  .welcome {
    width: 40%;
  }
  .intro {
    height: 60vh;
  }
  .info {
    margin: 5rem 0 5rem 0;
  }
  .show-header {
    height: 10rem;
  }
}
